import React, { useState, useEffect } from "react";
import { createCart, getProductCartStatus } from "../../library/parse";
import NumberTextInput from '../../containers/CRUDForm/NumberTextInput'
import { Button, Box } from "grommet";
const Component = ({ product }) => {
  const [productStatus, setProductStatus] = useState([]);
  const displayPrice =
    product.get("closePrice") && product.get("closePrice").toFixed(2);
  const qty = product.get("quantity");
  const loadProductCart = () => {
    getProductCartStatus(product).then((e) => setProductStatus(e));
  };
  const [buyQuantitiy, setBuyQuantity] = useState(1)

  useEffect(() => {
    loadProductCart();
  }, []);

  return (
    <>
      <Box pad="2px">{`$${displayPrice}`}</Box>
      <Box pad="2px">{`Qty: ${qty}`}</Box>

      {
        buyQuantitiy > 1 && <NumberTextInput 
        size="small"
        placeholder="buy how many?"
        onChange={(v) => setBuyQuantity(v)}
        />
      }
      <Button
        onClick={() => {
          createCart(product.id, product.get("closePrice"), buyQuantitiy);
          setTimeout(() => {
            loadProductCart();
          }, 0);
        }}
        label={productStatus.length ? "Sold Out" : "Buy"}
        disabled={productStatus.length}
        size="small"
        primary
      />
    </>
  );
};

export default Component;
